<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Add New Radio Equipments</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container">
                <div class="row q-col-gutter-md">
                    <div class="col-md-9 col-12 comp-grid">
                        <q-card   class="q-pa-md nice-shadow-18">
                            <div>
                                <ValidationObserver ref="observer" v-slot="{ invalid }" tag="form" @submit.prevent="startSaveRecord()" @reset="resetForm">
                                    <div class="row q-col-gutter-md">
                                        <div class="col-12">
                                            <div class="row">
                                                <div class="col-sm-3 col-12">
                                                    Amateur Operator Application Id *
                                                </div>
                                                <div class="col-sm-9 col-12">
                                                    <api-data-source @loaded="response => mapOptionField(response, 'amateur_operator_application_id')"  api-path="components_data/amateur_operator_application_id_option_list"  v-slot="req">
                                                        <ValidationProvider :rules="{required:true}" name="Amateur Operator Application Id" v-slot="{ errors, invalid, validated }">
                                                            <q-select   :loading="req.loading"  outlined dense  ref="ctrlamateur_operator_application_id" emit-value map-options  v-model="formData.amateur_operator_application_id" :options="req.response" label="Amateur Operator Application Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                            <template v-slot:option="scope">
                                                                <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                    <q-item-section v-if="scope.opt.image" avatar>
                                                                        <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                    </q-item-section>
                                                                    <q-item-section>
                                                                        <q-item-label />{{ scope.opt.label }}
                                                                            <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                        </q-item-section>
                                                                    </q-item>
                                                                </template>
                                                                <template v-slot:selected-item="scope">
                                                                    <q-item  v-if="scope.opt">
                                                                        <q-item-section v-if="scope.opt.image" avatar>
                                                                            <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                        </q-item-section>
                                                                        <q-item-section>
                                                                            <q-item-label />{{ scope.opt.label }}
                                                                                <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                            </q-item-section>
                                                                        </q-item>
                                                                    </template>
                                                                    </q-select> 
                                                                </ValidationProvider>
                                                            </api-data-source>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    <div class="row">
                                                        <div class="col-sm-3 col-12">
                                                            Make *
                                                        </div>
                                                        <div class="col-sm-9 col-12">
                                                            <ValidationProvider :rules="{required:true}" name="Make" v-slot="{ errors, invalid, validated }">
                                                                <q-input outlined dense  ref="ctrlmake" v-model="formData.make"  label="Make" type="text" placeholder="Enter Make"   list="make_list"  
                                                                class="" :error="invalid && validated" :error-message="errors[0]">
                                                                </q-input>
                                                                <datalist id="make_list">
                                                                <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                </datalist>
                                                            </ValidationProvider>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    <div class="row">
                                                        <div class="col-sm-3 col-12">
                                                            Model *
                                                        </div>
                                                        <div class="col-sm-9 col-12">
                                                            <ValidationProvider :rules="{required:true}" name="Model" v-slot="{ errors, invalid, validated }">
                                                                <q-input outlined dense  ref="ctrlmodel" v-model="formData.model"  label="Model" type="text" placeholder="Enter Model"   list="model_list"  
                                                                class="" :error="invalid && validated" :error-message="errors[0]">
                                                                </q-input>
                                                                <datalist id="model_list">
                                                                <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                </datalist>
                                                            </ValidationProvider>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    <div class="row">
                                                        <div class="col-sm-3 col-12">
                                                            Serial No *
                                                        </div>
                                                        <div class="col-sm-9 col-12">
                                                            <ValidationProvider :rules="{required:true}" name="Serial No" v-slot="{ errors, invalid, validated }">
                                                                <q-input outlined dense  ref="ctrlserial_no" v-model="formData.serial_no"  label="Serial No" type="text" placeholder="Enter Serial No"   list="serial_no_list"  
                                                                class="" :error="invalid && validated" :error-message="errors[0]">
                                                                </q-input>
                                                                <datalist id="serial_no_list">
                                                                <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                </datalist>
                                                            </ValidationProvider>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    <div class="row">
                                                        <div class="col-sm-3 col-12">
                                                            Frequency *
                                                        </div>
                                                        <div class="col-sm-9 col-12">
                                                            <ValidationProvider :rules="{required:true}" name="Frequency" v-slot="{ errors, invalid, validated }">
                                                                <q-input outlined dense  ref="ctrlfrequency" v-model="formData.frequency"  label="Frequency" type="text" placeholder="Enter Frequency"   list="frequency_list"  
                                                                class="" :error="invalid && validated" :error-message="errors[0]">
                                                                </q-input>
                                                                <datalist id="frequency_list">
                                                                <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                </datalist>
                                                            </ValidationProvider>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    <div class="row">
                                                        <div class="col-sm-3 col-12">
                                                            Note *
                                                        </div>
                                                        <div class="col-sm-9 col-12">
                                                            <ValidationProvider :rules="{required:true}" name="Note" v-slot="{ errors, invalid, validated }">
                                                                <q-input outlined dense  ref="ctrlnote" v-model="formData.note"  label="Note" type="text" placeholder="Enter Note"   list="note_list"  
                                                                class="" :error="invalid && validated" :error-message="errors[0]">
                                                                </q-input>
                                                                <datalist id="note_list">
                                                                <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                </datalist>
                                                            </ValidationProvider>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div v-if="showSubmitButton" class="text-center q-my-md">
                                                <q-btn type="submit"    :rounded="false"  color="primary"  no-caps  unelevated   :disabled="invalid" icon="send" :loading="saving">
                                                    <q-spinner-oval slot="loading" /> Submit
                                                </q-btn>
                                            </div>
                                        </ValidationObserver>
                                        <slot :submit="submit" :saving="saving"></slot>
                                    </div>
                                </q-card>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </template>
        <script>
	import { PageMixin } from "../../mixins/page.js";
	import { AddPageMixin } from "../../mixins/addpage.js";
	import { mapActions } from "vuex";
	export default {
		name: 'addRadioequipmentsPage',
		components: {
		},
		mixins: [PageMixin, AddPageMixin ],
		props:{
			pageName : {
				type : String,
				default : 'radio_equipments',
			},
			routeName : {
				type : String,
				default : 'radio_equipmentsadd',
			},
			apiPath : {
				type : String,
				default : 'radio_equipments/add',
			},
		},
		data() {
            return {
				formData: {
					amateur_operator_application_id: "", make: "", model: "", serial_no: "", frequency: "", note: "", 
				},
			}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Add New Radio Equipments"
				}
			}
		},
		meta () {
			return {
				title: this.pageTitle
			}
		},
		methods: {
			...mapActions('radio_equipments', ['saveRecord']),
			async startSaveRecord(){
				const isFormValid = await this.$refs.observer.validate();
				if(isFormValid){
					this.saving = true;
					let payload = this.normalizedFormData();
					let url = this.apiUrl;
					let data = { url, payload  }
					this.saveRecord(data).then((response) => {
						this.record = response.data;
						this.id = this.record['id'];
						this.saving = false;
						this.resetForm();
						this.closeDialogs();// close page dialog that if opened
						this.flashMsg(this.msgAfterSave);
						this.navigateTo(`/radio_equipments`);
					},
					 (response) => {
						this.saving = false;
						this.showPageRequestError(response);
					});
				}
			},
			resetForm (){
				this.formData = {amateur_operator_application_id: "", make: "", model: "", serial_no: "", frequency: "", note: "", };
				requestAnimationFrame(() => {
					this.$refs.observer.reset();
				});
				this.$EventBus.$emit("resetForm");
			},
		},
		mounted() {
		},
	};
</script>
<style scoped>
</style>
